<template>
    <v-echarts :chartData="chartData"></v-echarts>
</template>

<script>
    import vEcharts from './echarts'
    import _merge from 'lodash.merge'

    export default {
        name: 'chartBar',
        props: ['data'],
        data() {
            return {
                baseOptions: null,
                chartData:{
                    loading: true,
                    options: {
                        tooltip : {
                            trigger: 'axis',
                            axisPointer: { // 坐标轴指示器，坐标轴触发有效
                                type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
                            }
                        },
                        grid: {
                            top:'30px'
                        },
                        yAxis: [{
                            splitLine: {
                                //坐标轴在 grid 区域中的分隔线。
                                lineStyle: {
                                    type: 'dashed',
                                    color: '#e4e4e4'
                                }
                            },
                            axisLine: {
                                lineStyle: {
                                    color: '#333' //坐标轴轴线颜色
                                }
                            },
                            axisLabel: {
                                color: '#333',  //刻度标签文字的颜色。
                                fontSize: 12
                            }
                        }],
                        xAxis: [{
                            splitLine: {
                                //坐标轴在 grid 区域中的分隔线。
                                lineStyle: {
                                    color: ['#333']
                                }
                            },
                            axisLine: {
                                lineStyle: {
                                    color: '#333' //坐标轴轴线颜色
                                }
                            },
                            axisLabel: {
                                color: '#333', //刻度标签文字的颜色。
                                fontSize: 12
                            }
                        }],
                        series: [
                            {
                                type: 'bar',
                                data: []
                            }
                        ]
                    }
                }
            }
        },
        created(){
            // vue实例创建完成，先保存默认配置，因为后面配置将被覆盖
            this.baseOptions = Object.assign({},this.chartData)
        },
        watch: {
            'data.loading':{
                deep: true,
                handler(n, o) {
                    // 判断 loading 状态为false
                    if(!n){
                        // 基于默认配置复制 options 对象
                        let baseOptions = Object.assign({},this.baseOptions);
                        // 基于先前保存的默认配置进行配置的合并
                        this.chartData = _merge(baseOptions, this.data);
                    }else{
                        this.chartData.loading = n;
                    }
                }
            }
        },
        components: {
            vEcharts
        }
    }
</script>
